.text-carousel-card {
  background: url("../../images/ui-glyphs/stripes-black.svg") repeat-x left bottom !important;
  background-size: 18px 16px !important;
  background-color: #EC1F27 !important;
  color: white;
  order: 2;
  border-right: 1px solid white;
  background-origin: border-box;
}

.text-carousel-card h1 {
  text-transform: uppercase;
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 3rem;
}

.text-carousel-card div.text-carousel {
  position: relative;
}

.text-carousel-card div.text-carousel .control {
  position: absolute;
  background: url("../../images/ui-glyphs/caret-leftward.svg") no-repeat center center;
  background-size: 10px 18px;
  background-color: transparent;
  width: 40px;
  height: 40px;
  top: 50%;
  border: none;
  cursor: pointer;
  transition: all 200ms;
  transform: translateY(-50%) scale(1);
  z-index: 2;
}

.text-carousel-card div.text-carousel .control:hover {
  transform: translateY(-50%) scale(1.2);
}

.text-carousel-card div.text-carousel .control.prev {
  left: -10px;
}

.text-carousel-card div.text-carousel .control.next {
  right: -10px;
  background-image: url("../../images/ui-glyphs/caret-rightward.svg");
}

.text-carousel-card div.text-carousel ol.beliefs {
  position: relative;
  z-index: 1;
  min-height: 50px;
}

.text-carousel-card div.text-carousel ol.beliefs li {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
  font-size: 1.3rem;
  line-height: 1.1;
  box-sizing: border-box;
  padding: 0 40px;
  opacity: 0;
  transition: all 400ms;
  font-weight: 400;
  list-style: none;
}

.text-carousel-card div.text-carousel ol.beliefs li.active {
  opacity: 1;
}

.text-carousel-card .action {
  padding-top: 2rem;
}

.text-carousel-card .action a {
  cursor: pointer;
  transition: all 150ms;
  display: inline-block;
  text-transform: uppercase;
  color: white;
  padding: 12px 30px 12px;
  background: #EC1F27;
  font-family: "Manifold DSA", 'Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"';
  border-radius: 30px;
  font-weight: 700;
  font-size: 1.0625rem;
  border: none;
  cursor: pointer;
  transition: all 150ms;
}

.text-carousel-card .action a:hover {
  background: #f04e54;
  transform: translateY(-2px);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2);
}

